import { Component, Host, h } from '@stencil/core';

@Component({
  tag: 'tp-hero',
  styleUrl: 'hero.scss',
  scoped: true,
})
export class TpHero {
  /**
   * Parâmetros de configuração do swiper.
   */
  params = {
    slidesPerView: 1,
    navigation: true,
    rewind: true,
  };

  render() {
    return (
      <Host class="hero">
        <swiper-container class="hero__slider" {...this.params}>
          <slot />
        </swiper-container>
      </Host>
    );
  }
}
